  color: #000000;
}
button,input{
  border: none;
  outline: none;
}
html{
  /* 设计图是375像素， 1px = 0.26666667vw  100px = 26.666666666667vw*/
  font-size: 26.666666666666667vw;
}
body{
  background-color: #c4d3c4;
  min-width: 320px;
}
